/* eslint-disable no-restricted-imports */
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import {
  Paper,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Icon
} from "@material-ui/core";

import { Modal, Pagination, Button} from "antd";
import { Form } from "react-bootstrap";
import InputForm from "../../partials/common/InputForm";
import Loading from "../loading";
import ButtonLoading from "../../partials/common/ButtonLoading";
import { connect } from "react-redux";
import makeRequest from "../../libs/request";
import "./style.css";
import {
  showErrorMessage,
  showSuccessMessageIcon,
} from "../../actions/notification";

const useStyles1 = makeStyles((theme) => ({
  root: {
    width: "100%",
    marginTop: theme.spacing(3),
    overflowX: "auto",
  },
  table: {
    marginLeft: "5%",
    width: "90%",
  },
}));

const ListUser = (props) => {
  const classes1 = useStyles1();
  const rowsPerPage = 10;
  const [page, setPage] = useState(1);
  const [rows, setRow] = useState([]);
  const [dataSearch, setData] = useState({});
  const [dataDelete, setDataDelete] = useState({ visible: false });
  const [total, setTotal] = useState(0);
  const [isLoading, setLoading] = useState(true);
  const [isLoadDelete, setLoadDelete] = useState(false);
  const [showDetail, setShowDetail] = useState(false);
  const [dataDetail, setDataDetail] = useState({});
  const [typeOrder, setTypeOrder] = useState('name');
  const [stateOrder, setStateOrder] = useState('up');
  const [nameState, setNameState] = useState('up');
  const [emailState, setEmailState] = useState('up');
  const updateId = props.user.id;
  useEffect(() => {
    searchanization({ page: 1, limit: rowsPerPage, typeOrder, stateOrder });
  }, []);


  const searchanization = (dataSearch = {}) => {
    setLoading(true);
    makeRequest("get", `user/search`, dataSearch)
      .then(({ data }) => {
        if (data.signal) {
          const { listUsers, total } = data.data;
          setRow(listUsers);
          setTotal(total);
        }
        setLoading(false);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  };

  const handleChangePage = (newPage) => {
    setPage(newPage);
    searchanization({ ...dataSearch, page: newPage, limit: rowsPerPage });
  };

  const onChangeValue = (key, value) => {
    setData({
      ...dataSearch,
      [key]: value,
    });
  };

  const hideDeleteModal = () => {
    setDataDelete({
      ...dataDelete,
      visible: false,
      idDel: 0,
    });
  };

  const deleteAction = (id) => {
    setLoadDelete(true);
    hideDeleteModal();
    setLoadDelete(false);
    makeRequest("get", `user/delete`, { id: dataDelete.id })
      .then(({ data }) => {
        if (data.signal) {
          showSuccessMessageIcon("Xóa thành công");
          let dataOrg = rows.filter((item) => item.id !== dataDelete.id);
          setRow(dataOrg);
          hideDeleteModal();
        }
      })
      .catch((err) => {
        showErrorMessage("Xóa thất bại");
        console.log(err);
      });
  };

  const showModalDelete = (id) => {
    setDataDelete({
      id,
      visible: true,
    });
  };

  const unfilteredData = (e) => {
    setData({});
    setPage(1);
    searchanization({ page: 1, limit: rowsPerPage });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setPage(1);
    searchanization({ ...dataSearch, page: 1, limit: rowsPerPage });
  };

  const detailUser = (row) => {
    setShowDetail(true);
    setDataDetail(row);
  };

  const onOrder = (key, state) => {
    setTypeOrder(key);
    setStateOrder(state);
    if (key === 'name') {
      setNameState(state);
    }
    if (key === 'email') {
      setEmailState(state);
    }

    setPage(1);
    searchanization({ ...dataSearch, page: 1, limit: rowsPerPage, typeOrder: key, stateOrder: state });
  }

  return (
    <>
      <Link
        to="/user/add"
        className="btn btn-primary btn-bold btn-sm btn-icon-h kt-margin-l-10"
      >
        Tạo Người Dùng Mới
      </Link>
      {/* <Link
        to={`/user/update/${updateId}`}
        className="btn btn-primary btn-bold btn-sm btn-icon-h kt-margin-l-10"
      >
        Sửa Thông Tin Cá Nhân
      </Link> */}

      <div className="row">
        <div className="col-md-12">
          <div className="kt-section">
            <div className="kt-section__content">

              <Paper className={classes1.root} elevation={4}>
                <Paper className="lablePaper" elevation={0}>
                  <div className="labelList">
                    <label>Danh Sách Người Dùng</label>
                  </div>
                </Paper>

                <div className="col-md-12 part">
                  <Form onSubmit={handleSubmit}>

                    <div className="form-row">
                      <div className="form-group col-md-2 searchBox">
                        <div className="form-group">
                          <InputForm
                            type="text"
                            placeholder="Tìm theo tên người dùng"
                            value={dataSearch.name || ""}
                            onChangeValue={(value) => {
                              onChangeValue("name", value);
                            }}
                          />
                        </div>
                      </div>
                      <div className="form-group col-md-2 searchBox">
                        <div className="form-group">
                          <InputForm
                            type="text"
                            placeholder="Tìm theo email người dùng"
                            value={dataSearch.email || ""}
                            onChangeValue={(value) => {
                              onChangeValue("email", value);
                            }}
                          />
                        </div>
                      </div>
                      <div className="form-group col-md-2 searchBox">
                        <div className="form-group">
                          <InputForm
                            type="text"
                            placeholder="Tìm theo số điện thoại "
                            value={dataSearch.phone || ""}
                            onChangeValue={(value) => {
                              onChangeValue("phone", value);
                            }}
                          />
                        </div>
                      </div>
                      <div className="form-group col-md-3">
                        <div className="form-group" style={{ display: "flex" }}>
                          <button
                            className="btn btn-label-primary btn-bold btn-sm btn-icon-h kt-margin-l-10"
                            onClick={unfilteredData}
                            type="button"
                          >
                            <span>Bỏ lọc</span>
                          </button>

                          <ButtonLoading
                            type="submit"
                            className="btn btn-label-primary btn-bold btn-sm btn-icon-h kt-margin-l-10"
                            loading={isLoading}
                          >
                            <span>Tìm kiếm</span>
                          </ButtonLoading>
                        </div>
                      </div>
                    </div>
                  </Form>
                </div>
                {isLoading ? (
                  <Loading />
                ) : (
                    <Paper className="tablePaper" elevation={0}>
                      <Table className="table">
                        <TableHead>
                          <TableRow>
                            <TableCell >Tên người dùng (
                            {nameState === 'down' && <img onClick={() => onOrder('name', 'up')} src='/images/down-arrow.png' width={10} height={15} /> ||
                                <img onClick={() => onOrder('name', 'down')} src='/images/up-arrow.png' width={10} height={15} />})
                            </TableCell>
                            <TableCell  >Email (
                            {emailState === 'down' && <img onClick={() => onOrder('email', 'up')} src='/images/down-arrow.png' width={10} height={15} /> ||
                                <img onClick={() => onOrder('email', 'down')} src='/images/up-arrow.png' width={10} height={15} />})
                            </TableCell>
                            <TableCell  >Số điện thoại 
                            </TableCell>
                            <TableCell className="btnAction" >Action</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {rows && rows.length ? (
                            rows.map((row, key) => (
                              <TableRow key={`users-${key}`}>
                                <TableCell >{row.name}</TableCell>
                                <TableCell >{row.email}</TableCell>
                                <TableCell >{row.phone}</TableCell>
                                <TableCell>
                                  <span
                                    style={{ cursor: "pointer" }}
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    title="Detail"
                                  >
                                    <Icon
                                      className="fa fa-info-circle"
                                      onClick={(e) => {
                                        detailUser(row);
                                      }}
                                      style={{
                                        color: "#0000ff",
                                        fontSize: 15,
                                      }}
                                    />
                                  </span>
                                  <span
                                    style={{ cursor: "pointer" }}
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    title="Update"
                                  >
                                    <Icon
                                      className="fa fa-pen"
                                      onClick={() =>
                                        props.history.push(
                                          `/user/update/${row.id}`
                                        )
                                      }
                                      style={{
                                        color: "#ffa800",
                                        fontSize: 15,
                                        marginLeft: 15,
                                      }}
                                    />
                                  </span>
                                  <span
                                    style={{ cursor: "pointer" }}
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    title="Delete"
                                  >
                                    <Icon
                                      className="fa fa-trash"
                                      onClick={() => showModalDelete(row.id)}
                                      style={{
                                        color: "rgb(220, 0, 78)",
                                        fontSize: 15,
                                        marginLeft: 15,
                                      }}
                                    />
                                  </span>
                                </TableCell>
                              </TableRow>
                            ))
                          ) : (
                              <TableRow>
                                <TableCell colSpan={10} align="center">
                                  Không có dữ liệu
                          </TableCell>
                              </TableRow>
                            )}
                        </TableBody>
                      </Table>

                    </Paper>
                  )}
                {total > rowsPerPage && (
                  <div className="custom-svg customSelector">
                    <Pagination
                      className="pagination-crm"
                      current={page}
                      pageSize={rowsPerPage}
                      total={total}
                      onChange={(p, s) => handleChangePage(p)}
                    />
                  </div>
                )}
              </Paper>
            </div>
            <Modal
              title="Xóa Người Dùng"
              visible={dataDelete.visible}
              onOk={deleteAction}
              onCancel={hideDeleteModal}
              footer={[
                <ButtonLoading
                  type="default"
                  onClick={hideDeleteModal}
                  className="btn btn-label-secondary btn-secondary"
                >
                  Đóng
                </ButtonLoading>,
                <ButtonLoading
                  className="btn btn-label-danger btn-danger"
                  onClick={deleteAction}
                  loading={isLoadDelete}
                >
                  <span>Xóa</span>
                </ButtonLoading>,
              ]}
            >
              <p>Bạn có muốn xóa người dùng này?</p>
            </Modal>
            <Modal
              title="Chi tiết người dùng"
              visible={showDetail}
              width={600}
              onCancel={() => setShowDetail(false)}
              footer={[
                <Button type="primary" onClick={() => setShowDetail(false)}>
                  OK
                </Button>,
              ]}
            >
              <div className="row">
                <div className="col-md-12">
                  <span>Tên người dùng : {dataDetail.name}</span>
                </div>
                <div className="col-md-12">
                  <span>
                    Email người dùng: {dataDetail.email}
                  </span>
                </div>
                <div className="col-md-12">
                  <span>Số điện thoại người dùng: {dataDetail.phone}</span>
                </div>
              </div>

            </Modal>
          </div>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = ({ auth }) => ({
  user: auth.user,
});

export default connect(mapStateToProps, null)(ListUser);
